import React from 'react';
import { Link } from 'react-router-dom';
import Rings from '../components/Ring';
import styles from '../css/contact.module.css';
import { subscribe } from '../utils/mail';

export default function Contact() {

	return <>
		<section id='subscribe' className={styles.container}>
			<div className='wrapper' data-aos='fade'>
				<div className={styles.formContainer}  data-aos='fade-up' data-aos-easing='ease-out'>
					<form
						action='https://ethicardapp.us2.list-manage.com/subscribe/post?u=87204597a109849cc13ff23c7&amp;id=e930c1cf45' 
						method='post'
						id='mc-embedded-subscribe-form'
						name='mc-embedded-subscribe-form'
						target='_blank'
						className={styles.form}
						onSubmit={(e) => {
							e.preventDefault();
							let form = e.target as HTMLFormElement;
							form.submit();
							form.reset();
							return e;
							subscribe({ name: form.fName.value, email: form.email.value });
						}}
					>
						<h2>Subscribe</h2>
						<p>We’ll only send you fun stuff like when we’re launching, new businesses joining, or special offers (because too many emails are genuinely bad for the environment)</p>
						<input name='FNAME' type='text' placeholder='First Name*' required />
						<input name='LNAME' type='text' placeholder='Last Name*' required />
						<input name='EMAIL' type='email' placeholder='Email*' required />
						<label htmlFor='consent'>
							<input type='checkbox' required id='gdpr_17428' name='gdpr[17428]' />
							I agree to the{' \u200b'}<a href="https://www.iubenda.com/privacy-policy/95514654">Privacy Policy</a>
						</label>
						<input type='submit' value='Submit' />
					</form>
				</div>
			</div>
			<Rings className='bgRings' style={{ bottom: '0px' }} />
		</section>
		<section id={styles.spacer} />
	</>;
}